<template>
    <div class="home">
        <!-- 轮播图 -->
        <section class="rotation">
        <mt-swipe :auto="4000">
            <mt-swipe-item><img src="../images/home/sg.jpg" alt=""></mt-swipe-item>
            <mt-swipe-item><img src="../images/home/mg.jpg" alt=""></mt-swipe-item>
            <mt-swipe-item><img src="../images/home/ag.jpg" alt=""></mt-swipe-item>
            <mt-swipe-item><img src="../images/home/bg.jpg" alt=""></mt-swipe-item>
        </mt-swipe>
    </section>
    <!-- 搜索 -->
    <section class="search-box">    
        <span contentEditable="true"><i class="iconfont icon-sousuo"></i>搜索</span>
    </section>
    <!-- 产品分类菜单 -->
    <ul class="mango-sort-menu clearFix">
        <li><img src="../images/home/sort-bar.jpg" alt="">金百花</li>
        <li><img src="../images/home/sort-bicycle.jpg" alt="">椰香芒</li>
        <li><img src="../images/home/sort-festival.jpg" alt="">金煌芒</li>
        <li><img src="../images/home/sort-festival2.jpg" alt="">凯特芒</li>
        <li><img src="../images/home/sort-friend.jpg" alt="">贵妃芒</li>
        <li><img src="../images/home/sort-study.jpg" alt="">象牙芒</li>
        <li><img src="../images/home/fruit-dish.jpg" alt="">海顿芒</li>
        <li><img src="../images/home/sort-seabeach.jpg" alt="">芒果干</li>
    </ul>
    <!-- 热销芒果干 -->
    <section class="dried-mango-box">
        <h2 class="title">热销芒果干<a href="更多"></a></h2>
        <ul class="dried-mango-list">
            <li>
                <!-- <router-link >
                    <img src="" alt="">
                    <p class="name"></p>
                    <p class="price"></p>
                </router-link> -->
            </li>
        </ul>
    </section>
    </div>
</template>
<script>
import Vue from 'vue'
// 导入axios
import Axios from 'axios'
// 将axios挂载到Vue上
Vue.prototype.$axios = Axios





export default {
    name: "Home",
    data() {
        return {
            
        }
    },
    created() {
       this.$axios.get('../json/mangos.json', {
        params: {
            // get 参数可放置于此字段中，key-value对形式设置
            // 一般建议这么做，便于控制
        }
    }).then(res => {
        console.log(res)
        })
    .catch(error => {
        console.log(error);
        })
    }
}
</script>

<style lang="less" scoped>
@import '//at.alicdn.com/t/font_942460_244yszlm83j.css';
/* 轮播图 */
.rotation {
    width: 100%;
    height: 200px;
    img{
        width: 100%;
        height: 200px;
    }
}
/* 搜索 */
.search-box {
    width: 100%;
    height: 60px;
    background: #EDEEF1;
    
    span{
        display: inline-block;
        width: 90%;
        height: 40px;
        line-height: 40px;
        border-radius: 8px;
        margin: 10px 0;
        text-align: center;
        background: #fff;

        i{
            font-size: 22px;
        }
    }
}

/* 产品分类 */
.mango-sort-menu {
    margin: 0 15px;
    li{
        width: 17.5%;
        float: left;
        margin-bottom: 15px;
        img{
            background-color: #fff;
            width: 100%;
            border-radius: 50%;
            margin-bottom: 3px;
        }
    }
    li:not(:nth-child(4n)){
        margin-right: 10%;
    } 
    
}
</style>





