<template>
  <div id="app">
    <!-- 网页标题 -->
    <section class="title-bar">
      <h1 class="title">{{this.$store.state.title}}</h1>
    </section>
    <!-- 路由出口 -->
    <section class="content">
      <router-view/>
    </section>
    <!-- 路由链接 -->
    <ul class="menu-bar flex-row-col-cen">
      <li><router-link exact :to="urls.home"><i class="iconfont icon-home-page"></i>商城</router-link></li>
      <li><router-link :to="urls.sort"><i class="iconfont icon-leimupinleifenleileibie"></i>分类</router-link></li>
      <li><router-link :to="urls.cart"><i class="iconfont icon-daohanggouwuche"></i>购物车</router-link></li>
      <li><router-link :to="urls.mine"><i class="iconfont icon-wode"></i>我</router-link></li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      urls:{
        home: '/',
        sort: '/sort',
        cart: '/shoppingCart',
        mine: '/mine',
      }
    }
  }
}
</script>

<style lang="less">
@import './css/normalized.css';
@import '//at.alicdn.com/t/font_942460_s54fplhhcq.css';
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
}
// 标题
.title-bar{
  width: 100%;
  height: 64px;
  line-height: 64px;
  text-align: left;
  background: #fff;
  .title{
    font-size: 18px;
    font-weight: normal;
    padding-left: 15px;
  }
}
// 内容
.content{
  width: 100%;
  height: calc(100% - 64px - 49px);
  overflow-x: hidden;  
  overflow-y: scroll; 

  
}
// 底部导航
.menu-bar{
  width: 100%;
  padding: 6px;
  border-top: 1px solid #999;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 99;
  background: #fff;

  li{
    width: calc(100% / 4);
    height: 100%;
    
    a{
      display: block;
      width: 100%;
      height: 100%;
      font-size: 12px;
      color: #333;
      
      i {
        margin-bottom: 3px;
        display: block;
      }
    }
    a.active{
      color: #ED2D37;
    }
  }
}
</style>



