import Vue from 'vue'
import Router from 'vue-router'

// 导入四大主组件
import Home from '@/pages/home'
import Sort from '@/pages/sort'
import Cart from '@/pages/shoppingCart'
import Mine from '@/pages/mine'

Vue.use(Router)

export default new Router({
  linkActiveClass: 'active',
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/sort',
      name: 'sort',
      component: Sort
    },
    {
      path: '/shoppingCart',
      name: 'cart',
      component: Cart
    },
    {
      path: '/mine',
      name: 'mine',
      component: Mine
    }
  ]
})



